@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap);
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --first: '#208996';
  --second: '#576a77';
  --third: '#ced4da';
}

/* element styling */
/* link style  */
a {
  text-decoration: none;
  color: inherit;
}
a.active {
  background: #f8f9fa;
  color: #576a77 !important;
  width: 100%;
  height: 100%;
}
a.active:hover {
  color: #576a77;
}
a:hover {
  color: inherit;
}
.navbar-link:hover {
  color: #f8f9fa;
}

/* utility classess */
.first {
  color: #208996 !important;
}
.second {
  color: #576a77 !important;
}
.third {
  color: #ced4da;
  /* color: #e6f3fce3 !important; */
}
.forth {
  color: #e9ecef !important;
}
.fifth {
  color: #f8f9fa !important;
}
.bg-first {
  background: #208996 !important;
}
.bg-second {
  background: #576a77 !important;
}
.bg-third {
  background: #ced4da !important;
}
.bg-forth {
  background: #e9ecef !important;
}
.bg-fifth {
  background: #f8f9fa !important;
}

/* side Navbar styling */
.sideNavbar {
  position: fixed;
  z-index: 5;
  left: 0;
  top: 0;
  /* color: #60859d; */
}

/* navbar */
.navbar {
  position: fixed;
  z-index: 4;
  top: 0;
  right: 0;
  width: 100%;
  box-shadow: 0px 1px rgba(0, 0, 0, 0.25);
}
.footer {
  position: fixed;
  z-index: 4;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 20px 0;
}

/* overides MUI classess */
.css-rnmm7m-MuiPaper-root-MuiDi-paper {
  border-radius: 0 !important;
}
.css-1poimk-MuiPaper-root-MuiMenu-paper-MuiPaper-root-MuiPopover-paper {
  z-index: 5 !important;
}
/* table styling */
.MuiDataGrid-root .MuiDataGrid-viewport {
  background-color: #ededed;
}
.MuiDataGrid-root {
  border: none !important;
}
/* .MuiDataGrid-columnsContainer {
